import React from "react";
import { NavLink } from "react-router-dom";
import Footer from "../Footer/Footer";
import Header from "../Header/Header";
import './Login.css'

const Login = () => {
  return (
    <>
    <Header/>
    <div className="Login_Main_Page">
      <h3 className="LoginHeadding">User Login</h3>
    <form>
        <div class="form-outline mb-4">
          <input type="email" id="form2Example1" class="form-control" placeholder="Enter Mobile Number" />
        </div>
        <div class="form-outline mb-4">
          <input type="password" id="form2Example2" class="form-control" placeholder="Enter Password" />
        </div>
        <div class="row mb-4">
          <div class="col d-flex justify-content-center">
            <div class="form-check">
              <input class="form-check-input" type="checkbox" value="" id="form2Example31" />
              <label class="form-check-label" for="form2Example31"> Remember me </label>
            </div>
          </div>
          <div class="col">
            <a href="#!">Forgot password?</a>
          </div>
        </div>

        <button type="button" class="Loginbutton">Sign in</button>
        <div class="text-center">
          <p>Not a member? Please <NavLink to="/Register">Register</NavLink></p>
        </div>
      </form> 
    </div>
    <Footer/>
    </>
  );
};
  

export default Login;