import React from 'react'
import './slider.css';
import OwlCarousel from 'react-owl-carousel';  
import 'owl.carousel/dist/assets/owl.carousel.css';  
import 'owl.carousel/dist/assets/owl.theme.default.css';  
import CatBanner1 from '../../assets/images/slider/ChanaDal.jpg';
import CatBanner2 from '../../assets/images/slider/chocos.jpg';
import CatBanner3 from '../../assets/images/slider/gauridubar.jpg';
import CatBanner4 from '../../assets/images/slider/gauriMogra.jpg';
import CatBanner5 from '../../assets/images/slider/nescafe200.jpg';


function CategorySlider()      
    {  
      return (  
        <>
            <h3 className='HomeHeadding3'>Latest Offers</h3>
            <div className='container-fluid' >            
            <OwlCarousel className="owl-theme LatestSlider responsiveClass"  
             loop center margin={1} autoplay ={true} dots={true} items={3.2} touchDrag={true} lazyLoad={true} 
             animateOut={'fadeOut'} animateIn={'flipInX'}>  
            <div><img className="img" src= {CatBanner1}/></div>  
            <div><img className="img" src= {CatBanner2}/></div>  
            <div><img className="img" src= {CatBanner3}/></div>  
            <div><img className="img" src= {CatBanner4}/></div>  
            <div><img className="img" src= {CatBanner5}/></div>        
        </OwlCarousel>  
        </div> 
        </> 
      );  
    }  


export default CategorySlider  