import React from "react";
import { NavLink } from "react-router-dom";
import Footer from "../Footer/Footer";
import Header from "../Header/Header";
import './Register.css'

const Register = () => {
  return (
    <>
     <Header/>
      <div className="Register_Main_Page">
        <h3 className="RegisterHeadding">New User Registration</h3>
      <form>
          <div class="form-outline mb-4">
            <input type="email" id="form2Example1" class="form-control" placeholder="Enter User Name" />
          </div>
          <div class="form-outline mb-4">
            <input type="email" id="form2Example1" class="form-control" placeholder="Enter Mobile Number" />
          </div>
          <div class="form-outline mb-4">
            <input type="email" id="form2Example1" class="form-control" placeholder="Enter Email (Optional)" />
          </div>
          <div class="form-outline mb-4">
            <input type="password" id="form2Example2" class="form-control" placeholder="Enter Password" />
          </div>

          <button type="button" class="Registerbutton">Sign up</button>
          <div class="text-center">
            <p>If You are Register - Please <NavLink to="/Login">Login</NavLink></p>
          </div>
        </form> 
      </div>
      <Footer/>
    </>
  );
};
  
export default Register;